import Dep from "./uitils/Dep.js";
import { log_error } from "./uitils/log";
import { indexedDB, IDBTransaction, IDBKeyRange } from "./global";
import { isArray, isObject } from "./uitils/type.js";

class DB {
  constructor({ dbName, version }) {
    this.dbName = dbName;
    this.version = version;
    this.db = null;
    this.idb = null;
    this.table = [];
    this._status = false; // ÊòØÂê¶ÂÖàÊ∑ªÂä†‰∫ÜË°®
    this._dep_ = new Dep();
  }

  /**
   * ÊâìÂºÄÊï∞ÊçÆÂ∫ì
   * @success ÊàêÂäüÁöÑÂõûË∞ÉÔºåËøîÂõûdbÔºåÈùûÂøÖ‰º†
   * @error Â§±Ë¥•ÁöÑÂõûË∞ÉÔºåËøîÂõûÈîôËØØ‰ø°ÊÅØÔºåÈùûÂøÖ‰º†
   * */
  open(ops) {
    let success = () => {},
      error = () => {};

    if (ops) {
      success = ops.success ? ops.success : success;
      error = ops.error ? ops.error : error;
    }

    // ÊâìÂºÄÂâçË¶ÅÂÖàÊ∑ªÂä†Ë°®
    if (this.table.length == 0 && !this._status) {
      log_error("ÊâìÂºÄÂâçË¶ÅÂÖàÁî®add_tableÊ∑ªÂä†Ë°®");
      return;
    }

    if (typeof success !== "function") {
      log_error("open‰∏≠successÂøÖÈ°ªÊòØ‰∏Ä‰∏™functionÁ±ªÂûã");
      return;
    }

    const request = indexedDB.open(this.dbName, this.version);

    request.onerror = e => {
      error(e.currentTarget.error.message);
    };

    request.onsuccess = e => {
      this.db = e.target.result;
      success(this.db);
      this._dep_.notify();
    };

    request.onupgradeneeded = e => {
      this.idb = e.target.result;

      for (let i = 0; i < this.table.length; i++) {
        this.__create_table(this.idb, this.table[i]);
      }
    };
  }

  //  ÂÖ≥Èó≠Êï∞ÊçÆÂ∫ì
  close_db() {
    const handler = () => {
      this.db.close();
    };

    this.__action(handler);
  }

  // Âà†Èô§Êï∞ÊçÆÂ∫ì
  delete_db() {
    indexedDB.deleteDatabase(name);
  }

  //Ê∏ÖÁ©∫ÊüêÂº†Ë°®ÁöÑÊï∞ÊçÆ
  clear_table({ tableName }) {
    this.__action(() =>
      this.__create_transaction(tableName, "readwrite").clear()
    );
  }

  /**
   * Ê∑ªÂä†‰∏ÄÂº†Ë°®
   * @param tableOption<Object>
   * @tableName Ë°®Âêç
   * @option Ë°®ÈÖçÁΩÆ
   * @index Á¥¢ÂºïÈÖçÁΩÆ
   * */
  add_table(tableOption = {}) {
    this._status = false;
    this.table.push(tableOption);
  }

  /**
   * @method Êü•ËØ¢ÊüêÂº†Ë°®ÁöÑÊâÄÊúâÊï∞ÊçÆ
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Function} [success] @return {Array} Êü•ËØ¢ÊàêÂäüÁöÑÂõûË∞ÉÔºåËøîÂõûÊü•Âà∞ÁöÑÁªìÊûú
   * */
  queryAll({ tableName, success = () => {} }) {
    if (typeof success !== "function") {
      log_error("queryAllÊñπÊ≥ï‰∏≠successÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }

    const handler = () => {
      const res = [];

      this.__create_transaction(
        tableName,
        "readonly"
      ).openCursor().onsuccess = e =>
        this.__cursor_success(e, {
          condition: () => true,
          handler: ({ currentValue }) => res.push(currentValue),
          over: () => success(res)
        });
    };

    this.__action(handler);
  }

  /**
   * @method Êü•ËØ¢
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Function} condition Êü•ËØ¢ÁöÑÊù°‰ª∂
   *      @arg {Object} ÈÅçÂéÜÊØèÊù°Êï∞ÊçÆÔºåÂíåfilterÁ±ª‰ºº
   *      @return Êù°‰ª∂
   *   @property {Function} [success] @return {Array} Êü•ËØ¢ÊàêÂäüÁöÑÂõûË∞ÉÔºåËøîÂõûÊü•Âà∞ÁöÑÁªìÊûú
   * */
  query({ tableName, condition, success = () => {} }) {
    if (typeof success !== "function") {
      log_error("queryÊñπÊ≥ï‰∏≠successÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }

    if (typeof condition !== "function") {
      log_error("in query,condition is required,and type is function");
      return;
    }
    const handler = () => {
      let res = [];

      this.__create_transaction(
        tableName,
        "readonly"
      ).openCursor().onsuccess = e =>
        this.__cursor_success(e, {
          condition,
          handler: ({ currentValue }) => res.push(currentValue),
          over: () => success(res)
        });
    };

    this.__action(handler);
  }

  /**
   * @method Â¢ûÂä†Êï∞ÊçÆ
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Object} data ÊèíÂÖ•ÁöÑÊï∞ÊçÆ
   *   @property {Function} [success] ÊèíÂÖ•ÊàêÂäüÁöÑÂõûË∞É
   * */
  insert({ tableName, data, success = () => {} }) {
    if (!(isArray(data) || isObject(data))) {
      log_error("in insertÔºådata type is Object or Array");
      return;
    }

    if (typeof success !== "function") {
      log_error("insertÊñπÊ≥ï‰∏≠successÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }

    this.__action(() => {
      const store = this.__create_transaction(tableName, "readwrite");
      isArray(data) ? data.forEach(v => store.put(v)) : store.put(data);
      // this.__create_transaction(tableName, "readwrite").add(data);
      success();
    });
  }

  /**
   * @method Âà†Èô§Êï∞ÊçÆ
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Function} condition Êü•ËØ¢ÁöÑÊù°‰ª∂ÔºåÈÅçÂéÜÔºå‰∏éfilterÁ±ª‰ºº
   *      @arg {Object} ÊØè‰∏™ÂÖÉÁ¥†
   *      @return Êù°‰ª∂
   *   @property {Function} [success] Âà†Èô§ÊàêÂäüÁöÑÂõûË∞É  @return {Array} ËøîÂõûË¢´Âà†Èô§ÁöÑÂÄº
   *   @property {Function} [error] ÈîôËØØÂáΩÊï∞ @return {String}
   * */
  delete({ tableName, condition, success = () => {} }) {
    if (typeof success !== "function") {
      log_error("deleteÊñπÊ≥ï‰∏≠successÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }

    if (typeof condition !== "function") {
      log_error("in delete,condition is required,and type is function");
      return;
    }

    const handler = () => {
      let res = [];

      this.__create_transaction(
        tableName,
        "readwrite"
      ).openCursor().onsuccess = e =>
        this.__cursor_success(e, {
          condition,
          handler: ({ currentValue, cursor }) => {
            res.push(currentValue);
            cursor.delete();
          },
          over: () => {
            if (res.length == 0) {
              log_error(`in delete ,Êï∞ÊçÆÂ∫ì‰∏≠Ê≤°Êúâ‰ªª‰ΩïÁ¨¶ÂêàconditionÁöÑÂÖÉÁ¥†`);
              return;
            }
            success(res);
          }
        });
    };

    this.__action(handler);
  }

  /**
   * @method Âà†Èô§Êï∞ÊçÆ(‰∏ªÈîÆ)
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {String\|Number} target ÁõÆÊ†á‰∏ªÈîÆÂÄº
   *   @property {Function} [success] Âà†Èô§ÊàêÂäüÁöÑÂõûË∞É  @return {Null}
   * */
  delete_by_primaryKey({
    tableName,
    target,
    success = () => {},
    error = () => {}
  }) {
    if (typeof success !== "function") {
      log_error("in delete_by_primaryKeyÔºåsuccessÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }

    this.__action(() => {
      const request = this.__create_transaction(tableName, "readwrite").delete(
        target
      );
      request.onsuccess = () => success();
      request.onerror = () => error();
    });
  }

  /**
   * @method ‰øÆÊîπÊüêÊù°Êï∞ÊçÆ(‰∏ªÈîÆ)
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {String\|Number} target ÁõÆÊ†á‰∏ªÈîÆÂÄº
   *   @property {Function} handle Â§ÑÁêÜÂáΩÊï∞ÔºåÊé•Êî∂Êú¨Êù°Êï∞ÊçÆÁöÑÂºïÁî®ÔºåÂØπÂÖ∂‰øÆÊîπ
   *   @property {Function} [success] ‰øÆÊîπÊàêÂäüÁöÑÂõûË∞É   @return {Object} ËøîÂõûË¢´‰øÆÊîπÂêéÁöÑÂÄº
   * */
  update_by_primaryKey({ tableName, target, success = () => {}, handle }) {
    if (typeof success !== "function") {
      log_error("in update_by_primaryKeyÔºåsuccessÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }
    if (typeof handle !== "function") {
      log_error("in update_by_primaryKeyÔºåhandleÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }

    this.__action(() => {
      const store = this.__create_transaction(tableName, "readwrite");
      store.get(target).onsuccess = e => {
        const currentValue = e.target.result;
        handle(currentValue);
        store.put(currentValue);
        success(currentValue);
      };
    });
  }

  /**
   * @method ‰øÆÊîπÊï∞ÊçÆ
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Function} condition Êü•ËØ¢ÁöÑÊù°‰ª∂ÔºåÈÅçÂéÜÔºå‰∏éfilterÁ±ª‰ºº
   *      @arg {Object} ÊØè‰∏™ÂÖÉÁ¥†
   *      @return Êù°‰ª∂
   *   @property {Function} handle Â§ÑÁêÜÂáΩÊï∞ÔºåÊé•Êî∂Êú¨Êù°Êï∞ÊçÆÁöÑÂºïÁî®ÔºåÂØπÂÖ∂‰øÆÊîπ
   *   @property {Function} [success] ‰øÆÊîπÊàêÂäüÁöÑÂõûË∞ÉÔºåËøîÂõû‰øÆÊîπÊàêÂäüÁöÑÊï∞ÊçÆ   @return {Array} ËøîÂõûË¢´‰øÆÊîπÂêéÁöÑÂÄº
   * */
  update({ tableName, condition, handle, success = () => {} }) {
    if (typeof handle !== "function") {
      log_error("in update,handleÂøÖÈ°ªÊòØ‰∏Ä‰∏™functionÁ±ªÂûã");
      return;
    }

    if (typeof success !== "function") {
      log_error("in update,successÂøÖÈ°ªÊòØ‰∏Ä‰∏™functionÁ±ªÂûã");
      return;
    }

    if (typeof condition !== "function") {
      log_error("in update,condition is required,and type is function");
      return;
    }

    const handler = () => {
      let res = [];

      this.__create_transaction(
        tableName,
        "readwrite"
      ).openCursor().onsuccess = e =>
        this.__cursor_success(e, {
          condition,
          handler: ({ currentValue, cursor }) => {
            handle(currentValue);
            res.push(currentValue);
            cursor.update(currentValue);
          },
          over: () => {
            if (res.length == 0) {
              log_error(`in update ,Êï∞ÊçÆÂ∫ì‰∏≠Ê≤°Êúâ‰ªª‰ΩïÁ¨¶ÂêàconditionÁöÑÂÖÉÁ¥†`);
              return;
            }
            success(res);
          }
        });
    };
    this.__action(handler);
  }

  /**
   * @method Êü•ËØ¢Êï∞ÊçÆÔºà‰∏ªÈîÆÂÄºÔºâ
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Number|String} target ‰∏ªÈîÆÂÄº
   *   @property {Function} [success] Êü•ËØ¢ÊàêÂäüÁöÑÂõûË∞ÉÔºåËøîÂõûÊü•ËØ¢ÊàêÂäüÁöÑÊï∞ÊçÆ   @return {Object} ËøîÂõûÊü•Âà∞ÁöÑÁªìÊûú
   *
   * */
  query_by_primaryKey({ tableName, target, success = () => {} }) {
    if (typeof success !== "function") {
      log_error("in query_by_primaryKey,successÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }
    const handleFn = () => {
      this.__create_transaction(tableName, "readonly").get(
        target
      ).onsuccess = e => {
        const result = e.target.result;
        success(result || null);
      };
    };
    this.__action(handleFn);
  }

  /**
   * @method Êü•ËØ¢Êï∞ÊçÆÔºàÁ¥¢ÂºïÔºâ
   * @param {Object}
   *   @property {String} tableName Ë°®Âêç
   *   @property {Number|String} indexName Á¥¢ÂºïÂêç
   *   @property {Number|String} target Á¥¢ÂºïÂÄº
   *   @property {Function} [success] Êü•ËØ¢ÊàêÂäüÁöÑÂõûË∞ÉÔºåËøîÂõûÊü•ËØ¢ÊàêÂäüÁöÑÊï∞ÊçÆ   @return {Object} ËøîÂõûÊü•Âà∞ÁöÑÁªìÊûú
   *
   * */
  query_by_index({ tableName, indexName, target, success = () => {} }) {
    if (typeof success !== "function") {
      log_error("in query_by_index,successÂøÖÈ°ªÊòØ‰∏Ä‰∏™FunctionÁ±ªÂûã");
      return;
    }
    const handleFn = () => {
      this.__create_transaction(tableName, "readonly")
        .index(indexName)
        .get(target).onsuccess = e => {
        const result = e.target.result;
        success(result || null);
      };
    };
    this.__action(handleFn);
  }

  /**
   * @method Ê∏∏Ê†áÂºÄÂêØÊàêÂäü,ÈÅçÂéÜÊ∏∏Ê†á
   * @param {Function} Êù°‰ª∂
   * @param {Function} Êª°Ë∂≥Êù°‰ª∂ÁöÑÂ§ÑÁêÜÊñπÂºè @arg {Object} @property cursorÊ∏∏Ê†á @property currentValueÂΩìÂâçÂÄº
   * @param {Function} Ê∏∏Ê†áÈÅçÂéÜÂÆåÊâßË°åÁöÑÊñπÊ≥ï
   * @return {Null}
   * */
  __cursor_success(e, { condition, handler, over }) {
    const cursor = e.target.result;
    if (cursor) {
      const currentValue = cursor.value;
      if (condition(currentValue)) handler({ cursor, currentValue });
      cursor.continue();
    } else {
      over();
    }
  }

  /**
   * @method ÂºÄÂêØ‰∫ãÂä°
   * @param {String} Ë°®Âêç
   * @param {String} ‰∫ãÂä°ÊùÉÈôê
   * @return store
   * */
  __create_transaction(tableName, mode = "readwrite") {
    if (!tableName || !mode) {
      throw new Error("in __create_transaction,tableName and mode is required");
    }
    const transaction = this.db.transaction(tableName, mode);
    return transaction.objectStore(tableName);
  }

  // dbÊòØÂºÇÊ≠•ÁöÑ,‰øùËØÅfnÊâßË°åÁöÑÊó∂ÂÄôdbÂ≠òÂú®
  __action(handler) {
    const action = () => {
      handler();
    };
    // Â¶ÇÊûúdb‰∏çÂ≠òÂú®ÔºåÂä†ÂÖ•‰æùËµñ
    if (!this.db) {
      this._dep_.add(action);
    } else {
      action();
    }
  }

  /**
   * ÂàõÂª∫table
   * @option<Object>  keyPathÊåáÂÆö‰∏ªÈîÆ autoIncrementÊòØÂê¶Ëá™Â¢û
   * @index Á¥¢ÂºïÈÖçÁΩÆ
   * */
  __create_table(idb, { tableName, option, indexs = [] }) {
    if (!idb.objectStoreNames.contains(tableName)) {
      let store = idb.createObjectStore(tableName, option);
      for (let indexItem of indexs) {
        this.__create_index(store, indexItem);
      }
    }
  }

  /**
   * ÂàõÂª∫Á¥¢Âºï
   * @option<Object> uniqueÊòØÂê¶ÊòØÂîØ‰∏ÄÂÄº
   * */
  __create_index(store, { key, option }) {
    store.createIndex(key, key, option);
  }
}

export default DB;
